
import '../Navigation/Navigation.css';
import React from 'react';
import Media from 'react-media';

import nav_icon from '../../images/icons/nav_icon.svg';
import nav_logo from '../../images/icons/logo_lacost_textonly.png';

const Navigation = (props) => {

    const { bgOpacity } = props;

    const onClickHome = () => {
        props.changeRoute('home');
    }

    return (
        <nav id="nav-bar" style={bgOpacity} className="w-100  top-0 h2-s h3-ns fixed">
            <Media query="(min-width: 550px)">
                {matches =>
                    matches ? (
                        <div className='flex pt4 justify-between'>
                            <span className="flex items-center">
                                <img className="pl2 h2 ma0 pa0" alt="Logo" src={nav_logo}></img>
                            </span>
                            <div className="w-50 flex justify-end items-center pointer">
                                <button
                                    className="f2-l f4-m dib mr3"
                                    onClick={onClickHome}
                                >Inicio</button>
                                <button className="link f2-l f4-m dib mr3" >Productos</button>
                                <button className="link f2-l f4-m dib mr3" >Info</button>
                            </div>

                        </div>
                    ) : (
                            <span className='flex justify-start items-center'>
                                <img className="pl2 h2 ma0 pa0" alt="Nav_menu" src={nav_icon}></img>
                                <img className="pl2 h2 ma0 pa0" alt="Logo" src={nav_logo}></img>
                            </span>
                        )
                }
            </Media>


        </nav>
    );
}

export default Navigation;
